//_buttons.scss - Button style
button {
    @extend .reset-button;
}
.filter-button {
    font-family: 'DIN';
    font-size: 1em;
    color: $white;
    line-height: normal;
    text-transform: uppercase;
    text-align: center;
    background-color: $main-green--light;
    cursor: pointer;
    height: 2.188em;
    border-radius: 2em;
    padding: 0.688em 1.813em;
    opacity: 0.5;

    @media #{$mobile} {
        min-width: 7em;
        padding: 0.688em 1em;
        font-size: 90%;
    }

    &.active {
        background-color: $main-green;
        opacity: 1;
    }
    &:hover {
        background: transparent;
        color: $main-green;
        text-decoration: underline;
    }
}
.breadcrumb-button {
    @extend .reset-button;
    font-family: 'Avenir Heavy';
    font-size: 0.813em;
    color: $gray;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;

    &:hover,
    &:active {
        color: $gray;
        text-decoration: underline;
    }
}
.sidebar-button {
    font-family: 'Avenir Medium';
    font-size: 0.750em;
    color: $main-green--dark;
    text-align: center;
    cursor: pointer;
    background: transparent;
    padding: 0 1em;
    margin-bottom: 2.625em;

    &:last-child {
        margin-bottom: 0;
    }

    &:hover {
        opacity: 0.7;
    }
    .icon {
        width: 1.500em;
        height: 1.500em;
        display: block;
        margin: 0 auto 0.563em auto;

        .iconColor {
            fill: $main-green--dark;
        }
    }
    &.active {
        color: $white;

        .icon {
            .iconColor {
                fill: $white;
            }
        }
    }
}
.icon-button {
    font-family: 'Avenir Heavy';
    font-size: 0.875em;
    color: $gray;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    background: transparent;
    opacity: 0.5;

    &:hover {
        opacity: 0.2;
    }

    &.active {
        opacity: 1;
    }
    .icon {
        width: 1.750em;
        height: 1.750em;
        display: inline-block;
        margin-right: 0.625em;
        vertical-align: middle;
    }
}