//_home.scss - Home style
.sidebar {
    @media #{$mobile} {
        display: none;
    }
}
.middleSection {
    width: calc(100% - 6.875em);
    position: relative;
    left: 6.875em;
    padding-bottom: 6.000em;

    @media #{$mobile} {
        width: 100%;
        left: unset;
    }
}
.graphSection {
    .container {
        @extend .flexbox;
        flex-flow: row;

        @media #{$mobile} {
            flex-flow: column;
        }
    }
}