//_graph.scss - Graph style
.graph {
    @extend .flexbox;
    flex-flow: column;
    align-items: center;
    width: calc(100% - 9.375em);
    padding: 0.5em 0;

    @media #{$mobile} {
        width: 100%;
    }

    .total {
        margin-bottom: 3.313em;

        .total__label {
            color: $gray;
            font-family: 'DIN';
            font-size: 1.563em;
            text-align: center;
            text-transform: uppercase;
            display: inline-block;
            width: 100%;
        }
        .total__value {
            color: $main-green--light;
            font-family: 'Avenir Medium';
            font-size: 4.375em;
            text-align: center;

            @media #{$mobile} {
                font-size: 3.5em;
            }
        }
    }
    .graph__wrapper {
        @extend .flexbox;
        flex-flow: row;
        border-top: 2px solid $lighter-gray;
        padding: 2em 0 0 0;
        width: 100%;

        @media #{$mobile} {
            flex-flow: column;
        }

        .graph__title {
            color: $gray;
            font-family: 'DIN';
            font-size: 1.563em;
            text-align: center;
            text-transform: uppercase;
        }
        .graph__single {
            width: 48%;

            @media #{$mobile} {
                width: 100%;
                margin: 2.750em 0;
            }
        }
        .graph__legend {
            .legend__wrapper {
                @extend .flexbox;
                flex-flow: row;
                align-items: center;
                justify-content: space-between;
                width: 18.750em;
                margin: 0 auto;

                .legend__label {
                    color: $dark-gray;
                    font-family: 'Avenir Roman';
                    font-size: 0.625em;
                    text-transform: uppercase;
                    margin: 0;

                    span {
                        font-size: 350%;
                        vertical-align: middle;
                        display: inline-block;
                        margin-right: 0.2em;
                    }
                }
                .legend__value {
                    color: $dark-gray;
                    font-family: 'Avenir Black';
                    font-size: 0.750em;
                    text-align: right;
                    margin: 0;
                }

                &--magenta {
                    .legend__label span {
                        color: $magenta;
                    }
                }

                &--purple {
                    .legend__label span {
                        color: $purple;
                    }
                }

                &--green {
                    .legend__label span {
                        color: $greenBlue;
                    }
                }

                &--pink {
                    .legend__label span {
                        color: $pink;
                    }
                }
            }    
        }
        .graph__total {
            margin-top: 2.188em;

            .total__wrapper {
                @extend .flexbox;
                flex-flow: row;
                align-items: center;
                justify-content: space-between;
                width: 18.750em;
                margin: 0 auto;
            }
            .total__label,
            .total__value {
                color: $dark-gray;
                font-family: 'Avenir Black';
                font-size: 0.750em;
                text-align: right;
                margin: 0;
                text-transform: uppercase;
            }
        }
        .grap__loading {
            color: $main-green;
            font-family: 'Avenir Roman';
            font-size: 0.625em;
            text-transform: uppercase;
            text-align: center;
            margin: 5em 0;
        }
    }
}