//_graphFilters.scss - Graph Filter style
.graphFilters {
    padding: 0.5em 0;
    width: 9.375em;

    @media #{$mobile} {
        width: 100%;
    }

    .graphFilters__nav {
        .icon-button {
            display: block;
            margin-bottom: 1.625em;

            @media #{$mobile} {
                display: inline-block;
                margin-right: 1.625em;
                vertical-align: super;
            }

            &:last-child {
                margin-bottom: 0;

                @media #{$mobile} {
                    margin-right: 0;
                }
            }
        }
    }
}