//_topbar.scss - Topbar style
.topbar {
    background-color: $off-white;
    height: 100%;
    border-bottom: 1px solid $light-gray;

    .topbar__nav {
        @extend .flexbox;
        flex-flow: row;
        padding: 1.813em 0 2.313em 0;
        
        @media #{$mobile} {
            padding: 1.813em 0 1.125em 0;
        }
        .topbar__mobile {
            @media #{$desktop} {
                display: none;
            }
        }
        .breadcrumb-button {
            padding: 0 1.875em 0 1.875em;
            border-right: 1px solid $gray;
            
            @media #{$desktop} {
                &:nth-child(2) {
                    padding-left: 0;
                }
            }
            &:last-child {
                border-right: 0;
                padding-right: 0;
            }
        }
    }
}