//_filters.scss - Filters style
.filters {
    padding: 1.875em 0;

    @media #{$mobile} {
        overflow: hidden;
    }

    .filters__nav {
        @extend .flexbox;
        flex-flow: row;

        @media #{$mobile} {
            overflow-x: scroll;

            &::-webkit-scrollbar { 
                display: none;  // Safari and Chrome
            }
        }
        .filter-button {
            margin-right: 0.750em;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}