//_sidebar.scss - Sidebar style
.sidebar {
    background-color: $main-green;
    height: 100%;
    width: 6.875em;
    position: fixed;
    top: 0;
    left: 0;
    padding: 1em 0;
    @extend .flexbox;
    flex-flow: column;
    justify-items: center;
    align-items: center;

    .logo {
        margin-bottom: 1.875em;
    }
    .sidebar__nav {
        @extend .flexbox;
        flex-flow: column;
        justify-items: center;
        align-items: center;
    }
}